body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: auto;
 
  
}
html{
  scroll-behavior: smooth;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
nav{
  position: fixed !important;
  top: 0;
  width: 100%;
  border-bottom-left-radius: 4px;
  border-bottom-right-radius:4px;
  z-index: 10000;
  
}
.navbar-brand{
  font-size: 1.8rem !important;
}
.active1{
  color: aqua !important;
}
.active2{
  color:#ffc93c !important ;
}
.active3{
  color: #ea5455 !important;
}
.active4{
  color:#c2f7da !important;
}
#navbarSupportedContent{
  text-align: center;
 
}
.home-div{
  width: 50vw;
  height: 50vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.home-div .color{
  text-align: center;
  letter-spacing: 2px;
  word-spacing: 2px;
}
.home-div .color span{
  font-size: 3rem;
}


 
.sec1{
  background-image:linear-gradient(rgba(0,0,0,0.3),rgba(0,0,0,0.3)),url("photo.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;

  width: 100%;
  height: 92vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 8vh;
  
}
.sec2{
  background-color: #ffc93c;
  width: 100%;
  height: 92vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 8vh;
}
.sec3{
  background-color: #ea5455;
  width: 100%;
  height: auto;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 8vh;
}
.sec4{
  background-color: khaki;
  width: 100%;
  height: 92vh;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 8vh;
}
.color{
  color: white;
}
@media(max-width:768px){
  .sec1{
    width: 100%;
    height: 92vh;
    
  }
  section div h1{
    font-size: 1.7rem;
    
  }
  .navbar-brand{
    font-size: 2rem !important;
  }
  .home-div{
    width: 90vw;
  }
  
  

}
@media(width:1024px){
  .sec1{
    margin-top: 0;
  }
}
.seconds{
  width: 100%;
  height: 100vh;
  background-image:linear-gradient(rgba(0,0,0,0.3),rgba(0,0,0,0.3)), url("photo4.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #a2d5f2;
  text-align: center;
  
}
.secondsinfo{
  padding: 50px;
}

.info-div{
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin-top: 10px;
  color: #206a5d;
}

.imgs{
  display: flex;
  justify-content: center;
  align-items: center;
}
.tex{
  text-align: center;
  letter-spacing: 3px;
  text-transform: uppercase;
  text-shadow: 0px 0px 1px black;
  color: #ea5455;
}
.third{
  width: 100%;
  height: 100vh;
  background-image:linear-gradient(rgba(0,0,0,0.4),rgba(0,0,0,0.4)) ,url("photo2.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-attachment: fixed;
  background-position: center;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color: whitesmoke;

}
.third-info{
  padding: 20px;
  margin: 20px;
}
.third-info h1{
  color: #ea5455;
}
.third-info p{
  color: #ffc93c;
}
.third-info h4{
  color: #b2ebf2;
}
.fourth{
  width: 100%;
  height: 100vh;
  background-image: linear-gradient(rgba(0,0,0,0.4),rgba(0,0,0,0.4)),url("photo3.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  color:white
}
@media(max-width:768px){
  .fourth{
    height: auto;
  }
}
.info{
  color: #ea5455;
}
.info1{
  color: #ffd571;
}
.info2{
  color: #f8bd7f;
}
.info3{
  color: #ebdc87;
}
.footer-div{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 10vh;
  width: 100%;
  background-color:#557571 ;
  color: #f8bd7f;
  letter-spacing: 2px;
}
@media(max-width:768px){
  .footer-div{
    width: 100%;
    height: auto;
    font-size: 1.2rem;
    background-color:#557571 ;
    color: #f8bd7f;
    word-spacing: 1px;
  }
}
.about-info{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 40px;
  
}
.about-info h1{
  letter-spacing: 2px;
  color: white;
}
.about-info p{
  color: grey;
}
.about-info a{
  border: 2px solid white;
  padding: 5px 40px;
  border-radius: 50px;
  text-decoration: none;
  color: white;
  transition: 0.3s all;
  margin: 10px;
}
.about-info a:hover{
  background-color: white;
  color: #ffc93c;
}
.about-img img{
  clip-path: polygon(50% 0% , 100% 0% , 100% 100% ,20% 100%);
  box-shadow: 2px 2px 3px 3px rgba(0,0,0,1);
}
@media(max-width:768px){
  .sec2{
    width: 100%;
    height: auto;
  }
  .about-div{
    margin-top: 100px;
  }
  .about-img{
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .about-img img{
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
    width: 350px;
    height: 350px;
    border-radius: 50%;
    padding: 10px;
    box-shadow: none;
  }
  .about-info{
    text-align: center;
  }
  .about-info a{
    display: block;
  }
}
.about-seconds{
  width: 100%;
  height: 100vh;
}
.about-con{
  margin-top: 40px;

}
.about-team-head{
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 100px;
}
.about-seconds{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
.col-10{
  margin-bottom: 20px !important;
}
.seconds-info{
  display: flex;
  justify-content: center;
  align-items: center;
  letter-spacing: 2px;
  color: #ea5455;
  margin-top: 100px;

}
.services-info-con{
  margin: 100px;
}
.ser1{
  display: flex;
  justify-content: center;
  align-items: center;
}
.ser1 h1{
  color: white;
  letter-spacing: 4px;
}
.cont-img{
  font-size: 4rem;
  color: white;
 
}
@media(max-width:768px){
  .cont-img{
    display: flex;
    justify-content: center;
    align-items: center;
  }
}